// intro, music, feedback: secondary headings & text below them: 

.article {
  &__img {
    width: 100%;
    max-width: 770px;
    z-index: -1;
  }

  &__text {
    margin: auto;
    max-width: 541px;
    line-height: 1.5;
    text-align: center;
    color: $color-text-light-mode;
  }

  &__heading {
    padding: 50px 0 20px 0;
    font-weight: 200;
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__special {
    color: $accent-color-secondary;
  }

  &__paragraph {
    padding-bottom: 10px;

    &--marginBottom {
      margin-bottom: 50px;
    }
  }
   
  @media (min-width: 540px) {
    position: relative;
   
    &__img {
      display: block;
      margin-top: 120px;
      margin-left: auto;
    }

    &__text {
      transition: background-color .3s linear;
    }

    &__text--positioning {
      position: absolute;
      bottom: -200px;
      left: 0;
      padding: 0 40px 0 96px;
      text-align: start;
      background-color: $color-light-mode;
    }

    &__img--margin {
      margin: 180px 0 200px 0;
      margin-left: auto;
    }
  }
}